  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  overflow: auto;
  transform: translateX(-100%);
}

.page__aside:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

